@include pad('75', '');
@include pad('100', '');
@include pad('4', 'top');
@include pad('5', 'top');
@include pad('10', 'top');
@include pad('20', 'top');
@include pad('25', 'top');
@include pad('30', 'top');
@include pad('50', 'top');
@include pad('75', 'top');
@include pad('100', 'top');
@include pad('4', 'bottom');
@include pad('5', 'bottom');
@include pad('10', 'bottom');
@include pad('20', 'bottom');
@include pad('25', 'bottom');
@include pad('30', 'bottom');
@include pad('50', 'bottom');
@include pad('75', 'bottom');
@include pad('100', 'bottom');
@include pad('5', 'right');
@include pad('10', 'right');
@include pad('20', 'right');
@include pad('25', 'right');
@include pad('30', 'right');
@include pad('50', 'right');
@include pad('75', 'right');
@include pad('100', 'right');
@include pad('5', 'left');
@include pad('10', 'left');
@include pad('20', 'left');
@include pad('25', 'left');
@include pad('30', 'left');
@include pad('50', 'left');
@include pad('75', 'left');
@include pad('100', 'left');

@mixin no-padding($side) {
  @if $side== 'all' {
    .no-padding {
      padding: 0 !important;
    }
  } @else {
    .no-padding-#{$side} {
      padding-#{$side}: 0 !important;
    }
  }
}

@include no-padding('left');
@include no-padding('right');
@include no-padding('top');
@include no-padding('bottom');
@include no-padding('all');

/* end of padding helpers */

.no-margin {
  margin: 0px;
}
@mixin voffset($size) {
  @if $size== '' {
    .voffset {
      margin-top: 2px !important;
    }
  } @else {
    .voffset-#{$size} {
      margin-top: #{$size}px !important;
    }
  }
}

@include voffset('');
@include voffset('5');
@include voffset('10');
@include voffset('15');
@include voffset('30');
@include voffset('40');
@include voffset('60');
@include voffset('80');
@include voffset('100');
@include voffset('150');

.readonly {
  background-color: #eee;
  opacity: 1;
}

/* ==========================================================================
make sure browsers use the pointer cursor for anchors, even with no href
========================================================================== */

a:hover {
  cursor: pointer;
}

.hand {
  cursor: pointer;
}

button.anchor-btn {
  background: none;
  border: none;
  padding: 0;
  align-items: initial;
  text-align: initial;
  width: 100%;
}

a.anchor-btn:hover {
  text-decoration: none;
}

/* ==========================================================================
Metrics and Health styles
========================================================================== */

#threadDump .popover,
#healthCheck .popover {
  top: inherit;
  display: block;
  font-size: 10px;
  max-width: 1024px;
}

.thread-dump-modal-lock {
  max-width: 450px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

#healthCheck .popover {
  margin-left: -50px;
}

.health-details {
  min-width: 400px;
}

/* bootstrap 3 input-group 100% width
http://stackoverflow.com/questions/23436430/bootstrap-3-input-group-100-width */

.width-min {
  width: 1% !important;
}

.inline-block {
  display: inline-block;
  padding-left: 2em;
}

/* ==========================================================================
Planning page
========================================================================== */

.grid-container {
  display: grid;
  grid-template-rows: 3em repeat(auto-fit, minmax(20px, 1fr));
  grid-gap: 0;
  border-style: solid;
  border-width: 0.15em;
  border-color: #000;
  border-radius: 0.8em;
  > div {
    display: flex;
    align-items: center; /* vertically center children */
    justify-content: center; /* horizontally center children */
  }
}

.month {
  grid-row-start: 1;
  grid-row-end: 2;
  font-size: 1em;
  border-left-style: solid;
  border-left-width: 0.15em;
  border-color: #000;
}

.roomNames {
  grid-row-start: 2;
  grid-row-end: 4;
  grid-column-start: 1;
  grid-column-end: 4;
  text-align: center;
  border-top-style: solid;
  border-right-style: solid;
  border-width: 0.15em;
  border-color: #000;
}

.bedNames {
  grid-row-start: 2;
  grid-row-end: 4;
  grid-column-start: 4;
  grid-column-end: 8;
  border-top-style: solid;
  border-width: 0.15em;

  border-bottom-style: solid;
  border-color: #000;
  margin-bottom: -0.15em;
}

.beds {
  grid-column-start: 4;
  grid-column-end: 8;
  border-right-style: solid;
  border-bottom-width: 0.05em;
  border-bottom-style: solid;
  border-right-width: 0.15em;
  border-color: #000;
  margin-bottom: -0.12em;
  margin-right: -0.15em;
}

.rooms {
  grid-column-start: 1;
  grid-column-end: 4;
  border-right-style: solid;
  border-right-width: 0.15em;
  border-top-width: 0.15em;
  border-top-style: solid;
  border-color: #000;
}

.day {
  font-size: 0.8em;
  font-weight: bold;
  grid-row-start: 2;
  grid-row-end: 4;
  grid-column-start: 10;
  text-align: center;
  border-left-style: solid;
  border-left-width: 0.01em;
  border-color: #000;
  justify-items: center;
  align-items: center;
  min-height: 3.8em;
  border-top-style: solid;
  border-top-width: 0.05em;
}

.reservation {
  font-size: 0.8em;
  font-weight: bold;
  border-style: solid;
  border-width: 0.2em;
  border-radius: 1.5em;
  margin-left: 1.6em;
  margin-right: 1.6em;
  border-color: #114b5f;
}

#place {
  padding: '0.4rem';
  border-radius: '0.3rem';
  margin-left: '2rem';
}
/* jhipster-needle-scss-add-main JHipster will add new css style */

/* ==========================================================================
kitcnen page
========================================================================== */
.time {
  grid-column-start: 1;
  grid-column-end: 4;
  text-align: center;
  border-top-style: solid;
  border-right-style: solid;
  border-width: 0.15em;
  border-color: #000;
  height: 100%;
  width: 100%;
}

.lunch {
  grid-row-start: 4;
  grid-row-end: 6;
  padding: 8px;
}

.dinner {
  grid-row-start: 6;
  grid-row-end: 8;
  padding: 8px;
}

.diet {
  padding: 8px;
  grid-column-start: 4;
  grid-column-end: 8;
  border-top-style: solid;
  border-width: 0.15em;

  border-bottom-style: solid;
  border-color: #000;
  margin-bottom: -0.15em;

  border-right-style: solid;
}

.classicdietlunch {
  grid-row-start: 4;
  grid-row-end: 5;
}

.specialdietlunch {
  grid-row-start: 5;
  grid-row-end: 6;
}

.classicdietdinner {
  grid-row-start: 6;
  grid-row-end: 7;
}

.specialdietdinner {
  grid-row-start: 7;
  grid-row-end: 8;
}

.popup-comment {
  cursor: help;
}

.kitchenBtn {
  padding-left: 0;
  padding-right: 0;
}
